*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.main{
    height: 100vh;
    display: flex;
    align-items: center;
    flex-direction: column;
    color: #6b6b82;
    background-image: url("./Component/images/bg-desktop-dark.jpg");
    background-size: 100% 40%;
    background-repeat: no-repeat;
    background-color: #181824;
    padding-top: 5%;
}

.taskeInput{
    width: 200px;
    background-color: lightblue;
}

.container{
    height: 500px;
    width: 250px;
    border-radius: 10px;
}
h1{
    color: white;
    margin-bottom: 35px;
}
.header{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 35px;
    background-color: #25273c;
    background-position-x: 10px;
    border-radius: 5px;
}
input[type="text"], textarea {
    background-color : #25273c; 
    width: 200px;
    height: 35px;
    color: aliceblue;
    border-style: unset;
  }
  input:focus{
    outline: none;
  }
  /* .header button{
      border-radius: 50%;
      width: 20px;
      height: 20px;
      outline: none;
      border: 0 solid ;
  } */

.body{
    background-color: #25273c;
    width: 250px;
    height: 300px;
    margin-top: 20px;
    border-radius: 5px;
  }

.taskes{
     padding-top: 10px;
     display: flex;
     align-items: center;
     justify-content: center;
     padding-left: 20px;
     padding-bottom: 5px;
     color:white;
  }
  .taskes img{
      width: 10px;
      height: 10px;
      margin-right: 10px;
    
  }
  .taskes p{
      margin-right: auto;
  }
  hr {
    border: none;
    height: 0.3px;
    margin: 0 10px;
    background-color: rgba(156, 156, 156, 0.486); /* Modern Browsers */
}

